import React from 'react';
import './CVHeader.css';
import { Container as Header } from '@material-ui/core';

import * as logo from '../../resources/CVHeader/logo-white.png';
import * as background from '../../resources/Body/background_rsz.png';


/* colocar imagem no header depois o sticky ja funciona, mover  a imagem para dar cover do filler superior */

const CVHeader = () => {
  return [
    <>  
      
      <div style={{height:"15vh", position:"relative"}} />
      <Header className="CVHeader" maxWidth="false">
        <img src={background} className="CVHeader-Background" alt="logo-header" />
        <img src={logo} className="CVHeader-Logo-Img" alt="logo-header" />
      </Header>
    </>
  ];
};

export default CVHeader;

